/*
 * Checkboxes and radios
 */

.control {
  position: relative;
  display: inline-block;
  padding-left: 1.5rem;
  cursor: pointer;
}
.control input {
  position: absolute;
  opacity: 0;
  z-index: -1; /* Put the input behind the label so it doesn't overlay text */
}
.control-indicator {
  position: absolute;
  top: .325rem;
  left: 0;
  display: block;
  width:  1rem;
  height: 1rem;
  line-height: 1;
  // font-size: 50%;
  color: neutral(1);
  text-align: center;
  background-color: $white;
  background-size: 50% 50%;
  background-position: center center;
  background-repeat: no-repeat;
  pointer-events: none;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

/* Hover state */
/* Uncomment if you need it, but be aware of the sticky iOS states.
.control:hover .control-indicator {
  color: $white;
  background-color: neutral(1);
}
*/

/* Default */
.control .control-indicator {
  border: 2px solid neutral(2);
}

/* Focus */
.control input:focus ~ .control-indicator {
  box-shadow: 0 0 0 .075rem $white, 0 0 0 .2rem color(brand-light);
}

/* Checked state */
.control input:checked ~ .control-indicator {
  color: $white;
  background-color: color(brand-light);
  border: 0 none;
}

/* Active */
.control input:active ~ .control-indicator {
  color: $white;
  background-color: tint(color(brand-light),25%);
  border: 0 none;
}

/* Checkbox modifiers */
.checkbox .control-indicator {
  border-radius: .25rem;
}
.checkbox input:checked ~ .control-indicator {
  background-image: url(data:image/svg+xml;base64,);
  border: 0 none;
}

/* Radio modifiers */
.radio .control-indicator {
  border-radius: 50%;
}
.radio input:checked ~ .control-indicator {
  background-image: url(data:image/svg+xml;base64,);
  border: 0 none;
}

/* Alternately, use another character */
.control-x input:checked ~ .control-indicator {
  background-image: url(data:image/svg+xml;base64,);
  border: 0 none;
}
.control-dash input:checked ~ .control-indicator {
  background-image: url(data:image/svg+xml;base64,);
  border: 0 none;
}


/*
 * Select
 */

.select {
  position: relative;
  display: inline-block;
}

.select select,
.vfb-form-group > select {
  display: inline-block;
  width: 100%;
  margin: 0;
  padding: .5rem 2.25rem .5rem 1rem;
  line-height: 1.5;
  color: neutral(5);
  // background-color: neutral(1);
  border: 0;
  border-radius: .25rem;
  cursor: pointer;
  outline: 0;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;

  @include paper-input;

  // Undo the Firefox inner focus ring
  &:focus:-moz-focusring {
    color: transparent;
    text-shadow: 0 0 0 $black;
  }

  // Focus */
  // &:focus {
  //   box-shadow: 0 0 0 .075rem $white, 0 0 0 .2rem color(brand-light);
  // }

  /* Active/open */
  &:active {
    color: $white;
    background-color: color(brand-light);
  }

  /* Hide the arrow in IE10 and up */
  &::-ms-expand {
    display: none;
  }

  // Dropdown arrow
  &:after {
    position: absolute;
    top: 50%;
    right: 1.25rem;
    display: inline-block;
    content: "";
    width: 0;
    height: 0;
    margin-top: -.15rem;
    pointer-events: none;
    border-top: .35rem solid;
    border-right: .35rem solid transparent;
    border-bottom: .35rem solid transparent;
    border-left: .35rem solid transparent;
  }
}

/* Hover state */
/* Uncomment if you need it, but be aware of the sticky iOS states.
.select select:hover {
  background-color: neutral(1);
}
*/

/* Media query to target Firefox only */
@-moz-document url-prefix() {
  /* Firefox hack to hide the arrow */
  .select select,
  .vfb-form-group > select {
    text-indent: 0.01px;
    text-overflow: '';
    padding-right: 1rem;
  }

  /* <option> elements inherit styles from <select>, so reset them. */
  .select option,
  .vfb-form-group > select option {
    background-color: $white;
  }
}

/* IE9 hack to hide the arrow */
@media screen and (min-width:0\0) {
  .select select,
  .vfb-form-group > select {
    z-index: 1;
    padding: .5rem 1.5rem .5rem 1rem;

    &::after {
      z-index: 5;
    }
    &::before {
      position: absolute;
      top: 0;
      right: 1rem;
      bottom: 0;
      z-index: 2;
      content: "";
      display: block;
      width: 1.5rem;
      background-color: neutral(1);
    }
    &:hover,
    &:focus,
    &:active {
      color: neutral(5);
      background-color: neutral(1);
    }
  }
}



/*
 * File
 */

.file {
  position: relative;
  display: inline-block;
  cursor: pointer;
  height: 2.5rem;
}
.file input {
  min-width: 14rem;
  margin: 0;
  filter: alpha(opacity=0);
  opacity: 0;
}
.file-custom {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  z-index: 5;
  height: 2.5rem;
  padding: .5rem 1rem;
  line-height: 1.5;
  color: neutral(5);
  background-color: $white;
  border: .075rem solid neutral(1);
  border-radius: .25rem;
  box-shadow: inset 0 .2rem .4rem rgba(0,0,0,.05);
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.file-custom:after {
  content: "Choose file...";
}
.file-custom:before {
  position: absolute;
  top: -.075rem;
  right: -.075rem;
  bottom: -.075rem;
  z-index: 6;
  display: block;
  content: "Browse";
  height: 2.5rem;
  padding: .5rem 1rem;
  line-height: 1.5;
  color: neutral(5);
  background-color: neutral(1);
  border: .075rem solid neutral(1);
  border-radius: 0 .25rem .25rem 0;
}

/* Focus */
.file input:focus ~ .file-custom {
  box-shadow: 0 0 0 .075rem $white, 0 0 0 .2rem color(brand-light);
}



/*
 * Progress
 */

.progress {
  display: inline-block;
  height: 1rem;
}
.progress[value] {
  /* Reset the default appearance */
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  /* Remove Firefox and Opera border */
  border: 0;
  /* IE10 uses `color` to set the bar background-color */
  color: color(brand-light);
}
.progress[value]::-webkit-progress-bar {
  background-color: neutral(1);
  border-radius: .2rem;
}
.progress[value]::-webkit-progress-value {
  background-color: color(brand-light);
  border-top-left-radius: .2rem;
  border-bottom-left-radius: .2rem;
}
.progress[value="100"]::-webkit-progress-value {
  border-top-right-radius: .2rem;
  border-bottom-right-radius: .2rem;
}

/* Firefox styles must be entirely separate or it busts Webkit styles. */
@-moz-document url-prefix() {
  .progress[value] {
    background-color: neutral(1);
    border-radius: .2rem;
  }
  .progress[value]::-moz-progress-bar {
    background-color: color(brand-light);
    border-top-left-radius: .2rem;
    border-bottom-left-radius: .2rem;
  }
  .progress[value="100"]::-moz-progress-bar {
    border-top-right-radius: .2rem;
    border-bottom-right-radius: .2rem;
  }
}

/* IE9 hacks to accompany custom markup. We don't need to scope this via media queries, but I feel better doing it anyway. */
@media screen and (min-width:0\0) {
  .progress {
    background-color: neutral(1);
    border-radius: .2rem;
  }
  .progress-bar {
    display: inline-block;
    height: 1rem;
    text-indent: -999rem; /* Simulate hiding of value as in native `<progress>` */
    background-color: color(brand-light);
    border-top-left-radius: .2rem;
    border-bottom-left-radius: .2rem;
  }
  .progress[width="100%"] {
    border-top-right-radius: .2rem;
    border-bottom-right-radius: .2rem;
  }
}


/*
 * Control layouts
 */

.controls-stacked {
  margin: 1rem 0;
}
.controls-stacked .control,
.controls-stacked .progress {
  display: block;
}
.controls-stacked .control + .control,
.controls-stacked .progress + .progress {
  margin-top: .5rem;
}

.controls-inline {
  margin: 1rem 0;
}
.controls-inline .control {
  display: inline-block;
  height: 1rem;
}
.controls-inline .control + .control {
  margin-left: 1rem;
}
